import React from 'react'
import {Link} from 'react-router-dom'

const Navbar = () => {
    return (
        <nav className='teal'>
            <div className="nav-wrapper">
                <Link to="/" className="brand-logo center">Recipes`Finder</Link>
                <ul className="right hide-on-med-and-down">
                    <li className=''><Link to="/favorite"><i className="material-icons left">favorite</i>Favorite</Link></li>
                    <li className=''><Link to="/coockinglist"><i className="material-icons right">format_list_bulleted</i>Cooking list</Link></li>
                </ul>
            </div>
        </nav>
    )
}

export default Navbar